import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

//let cartname = window.localStorage.getItem('cartname')

export default new Vuex.Store ({
  state: {
      city: localStorage.city || 'Adelaide',    // 如果localstorage没有值就等于adelaide
      location: localStorage.location,           // localstorage用来存储location的值，refresh之后持续显示
      lat: '',
      lng: '',
      id: [],
      cartname: [],
      date: '',
      totalAmount: 0
  },
  actions: {
      changeCity(value, city) {
          value.commit('changedCity', city)
      }
  },
  mutations: {
      changedCity (state, city) {
          state.city = city
          localStorage.city = city
      },
      citycheckout (state, e) {
          state.location = e
          localStorage.location = e
      },
      latchange (state, lt) {
          state.lat = lt
      },
      lngchange (state, lg) {
          state.lng = lg
      },
      storeid (state, i) {
          state.id = i
      },
      addToCart (state, item) {
          let found = state.cartname.find(product => product.productId == item.productId );

          if(found){
              found.productQuanlity++;
          }else{
              state.cartname.push(item)

          }

          //this.commit('storeCartLocal')
      },
      // storeCartLocal (state) {
      //     window.localStorage.setItem('cartname', JSON.stringify(state.cartname))
      // },
      removeItem (state, item) {
          let index = state.cartname.indexOf(item)
          state.cartname.splice(index, 1)
      },
      totalAmount (state, amount) {
          state.totalAmount = state.totalAmount + amount
          localStorage.totalAmount = localStorage.totalAmount + amount
      },
      recountAccount (state, amount) {
          state.totalAmount = state.totalAmount - amount
          localStorage.totalAmount = localStorage.totalAmount - amount
      },
      storedate (state, d) {
          state.date = d
      }
  }
})



